import * as Types from './mutations-type';

export default {
  [Types.ADDCART](state, book) {
    let res = state.cart.find(item => item.bookId === book.bookId);
    if (res) {
      res.bookCount ++;
      state.cart = [...state.cart];
    } else {
      book.bookCount = 1;
      state.cart.push(book);
    }
    window.localStorage.setItem("cart", JSON.stringify(state.cart));
  },

  [Types.ADDCOUNT](state, id) {
    let res = state.cart.map(item => {
      if (item.bookId === id) {
        item.bookCount += 1;
        return item;
      } else {
        return item;
      }
    });
    state.cart = res;
    window.localStorage.setItem("cart", JSON.stringify(state.cart));
  },

  [Types.REDUCECOUNT](state, id) {
    let res = state.cart.find(item => item.bookId === id);
    if (res.bookCount === 1) return;
    res = state.cart.map(item => {
      if (item.bookId === id) {
        item.bookCount -= 1;
        return item;
      } else {
        return item;
      }
    });
    state.cart = res;
    window.localStorage.setItem("cart", JSON.stringify(state.cart));
  },

  [Types.REMOVECART](state,id) {
    state.cart = state.cart.filter(item => item.bookId !== id);
    window.localStorage.setItem("cart", JSON.stringify(state.cart));
  },

  [Types.CLEAR](state){
    state.cart = [];
    window.localStorage.setItem("cart", JSON.stringify(state.cart));
  }
};



